.speech-bubble {
  position: relative;
    font-family: arial;
    font-size: 1.1em;
    background: #c1e8fa;
    color: #fff;
    border-radius: 10px;
    padding: 20px;     
    display: block;
    margin: 15px auto;
    color: black;
    -webkit-animation-duration: .25s;
            animation-duration: .25s;
}

.speech-bubble.root {
  position: relative;
    font-family: arial;
    font-size: 1.1em;
    background: #a1c4fd;
    color: #fff;
    border-radius: 10px;
    padding: 5px;     
    display: block;
    margin: 15px auto;
    color: black;
    -webkit-animation-duration: .25s;
            animation-duration: .25s;
    text-align: center;
    font-weight:bold;
    font-style: italic;
}

.speech-bubble.root .fa-moon-o{
